// @ts-nocheck
import { useEffect, useState } from "react";
import { FileInfo } from "./lib";
import { Buffer } from "buffer";
import findVideos from "ighar";

interface Props {
  fileInfo: FileInfo;
}

const getVideos = (harFileContent: string) => {
  return findVideos(harFileContent).map(({ filename, output }) => {
    const dataUrl = `data:video/mp4;base64,${output.toString("base64")}`;
    return { filename, dataUrl };
  });
};

const HarFileInfo = (props: Props) => {
  const { fileInfo } = props;

  const [videos, setVideos] = useState([]);

  useEffect(() => {
    const videos = getVideos(fileInfo.text);
    setVideos(videos);
  }, []);

  console.log("videos", videos);

  return <>
    <ul>
      {videos.map(({ filename, dataUrl }) => (
        <li key={filename}>
          <a href={dataUrl} download={filename}>{filename}</a>
        </li>
      ))}
    </ul>
  </>

};
export default HarFileInfo;