import Buffer from "buffer";

const findVideos = (harFileContent) => {
  const har = JSON.parse(harFileContent);

  const basename = (path) => {
    const parts = path.split("/");
    return parts[parts.length - 1];
  };

  const entriesByFilename = har.log.entries.map((entry) => {
    const { request } = entry,
      { queryString, method, url } = request,
      filename = basename(url.split("?")[0]),
      bytestart = queryString.find(({ name }) => name === "bytestart"),
      byteend = queryString.find(({ name }) => name === "byteend");

    // Keep only mp4 entries    
    if (!(method === "GET" && bytestart?.value && byteend?.value && filename.endsWith(".mp4"))) {
      return;
    }

    return {
      bytestart: parseInt(bytestart.value),
      byteend: parseInt(byteend.value),
      text: entry.response.content.text,
      encoding: entry.response.content.encoding,
      filename,
    };
  }).filter(Boolean)
    // Group by filename
    .reduce((acc, offset) => {
      const { filename } = offset;
      if (!acc[filename]) {
        acc[filename] = [];
      }
      acc[filename].push(offset);
      return acc;
    }, {});


  const res = [];
  for (const filename in entriesByFilename) {
    const entries = entriesByFilename[filename];
    entries.sort((a, b) => a.bytestart - b.bytestart);
    if (entries[0].bytestart !== 0) {
      console.log("skipping", filename, "because it doesn't start at 0");
      continue;
    }
    const outputEntries = [entries[0]].concat(entries.map(e => {
      const { byteend } = e;
      return entries.find((e) => e.bytestart === byteend + 1);
    }).filter(Boolean));

    const buffers = outputEntries.map((e) => Buffer.Buffer.from(e.text, e.encoding));
    const output = Buffer.Buffer.concat(buffers);
    console.table(Object.entries(outputEntries).map(([k, v]) => ({
      bytestart: v.bytestart,
      byteend: v.byteend,
    })));
    res.push({ filename, output });
  }

  return res;
};

export default findVideos;